<template>
  <div class="userAssets" v-loading="load">

    <div class=" flex align between">
      <div class="menmuTitle flex align between">
        <h1 class="hasBack flex align"><i class="el-icon-back hoverTr3" @click="goback()" title="返回"></i> 分享记录</h1>
      </div>
      <!-- menmuTitle -->
    </div>

    <div class="space20"></div>

    <div class="listItem" v-if="dataList.length">
      <table>
        <thead>
          <tr>
            <th class="textLeft">用户帐号</th>
            <th class="textCenter">用户名</th>
            <th class="textRight" >推荐日期</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,index) in dataList" :key="index">
            <td class="textLeft">{{item.registerAccount}}</td>
            <td class="textCenter">{{item.userName}}</td>
            <td class="textRight">{{item.registerTime}}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- assetsTable -->

    <div v-if="dataList.length==0" class="nullData"><span>暂无数据</span></div>

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import {
    shareList as getList
  } from '../data/apis';


  export default {
    data() {
      return {
        dataList:[],
        load:false
      }
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      getData(){//获取数据
        this.dataList = [];
        this.load = true;
        getList().then(v=>{
          this.dataList = v.data;
          this.load = false;
        })
      },//getData
    },
    mounted() {
      this.getData();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  .hasBack {
    i {
      color: $them_color;
      font-size: 20px;
      margin-right: 20px;
      cursor: pointer;
    }
  }

  .search {
    margin-top: 20px;

    .el-select {
      margin-right: 20px;
    }
  }

</style>



